import { Container } from "react-bootstrap";
import { useState } from "react";
import Page from "../Templates/Page";
import FormCadFornecedor from "./Formularios/FormCadFornecedor";
import TabelaFornecedores from "./Tabelas/TabelaFornecedores";
import "./Telas.css";



export default function TelaCadastroFornecedor(props) {
    const [exibirFormulario, setExibirFormulario] = useState(false);
    return (
        <div className="tela-cad">
            <Container>
                <Page>
                    {
                        //dinmica em que o usurio ir alternar entre o formulrio de cadastro
                        //e a visualizao do registros j cadastrados.
                        exibirFormulario ? <FormCadFornecedor exibirFormulario={setExibirFormulario} /> : <TabelaFornecedores exibirFormulario={setExibirFormulario} />
                    }
                </Page>
            </Container>
        </div>
    )
}